<template>
	<yd-layout>
		<div id="OrderTracking">
			<yd-timeline>
				<yd-timeline-item>
					<p>【南宁市】您的订单正在配送途中，请您准备签收，感谢您的耐心等待。京东扫码付，单单享立减。</p>
					<p style="margin-top: 10px;">2017-08-18 08:24:18</p>
				</yd-timeline-item>
				<yd-timeline-item>
					<p>【南宁市】您的订单已到达【南宁安吉站】</p>
					<p style="margin-top: 10px;">2017-08-18 07:25:08</p>
				</yd-timeline-item>
				<yd-timeline-item>
					<p>【南宁市】您的订单在京东【南宁分拨中心】发货完成，准备送往京东【南宁安吉站】</p>
					<p style="margin-top: 10px;">2017-08-17 21:44:08</p>
				</yd-timeline-item>
			</yd-timeline>
		</div>
	</yd-layout>
</template>
<script>
	export default {
		data() {
			return {

			}
		},
		created() {
			this.$store.dispatch('changeNavBarTitle', '订单跟踪')
		},
		mounted() {

		},
		methods: {
			toOrderCheckoutSuccess() {
				this.$router.push({
					name: 'OrderCheckoutSuccess'
				})
			}
		}
	};
</script>
<style lang="scss">
	#OrderTracking {}
</style>


